/*ART 230 CSS*/

.art230 {
    font-family: sans-serif, Verdana, Helvetica, Arial;
    font-weight: normal;
    padding: 10px;
    text-align: left;
    line-height: 1.5em;
    font-size: 16px;
    background-color: white;
    max-width: 75%;
    --color1: #7c7c7c;
    --color24: #96887a;
    --color35: #7c7c7c;
    --button-color: #b0a8a1;
    --banner-color: var(--button-color);
}

/*Heading Styles*/

.art230 h2 {
    color: #cb5417;
    font-size: 1.25em;
    padding-bottom: .5em;
    margin-bottom: .5em;
    border-bottom-color: #dedbd1;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    clear: both;
    padding-top: 1.5em;
}

.art230 h3 {
    margin-top: 1em;
    margin-bottom: .5em;
    font-size: 1.12em;
    color: black;
    letter-spacing: 1px;
    clear: both;
}

.art230 h4 {
    font-size: 1em;
    margin-bottom: 0.5em;
    margin-top: 1em;
}

.art230 h5 {
    padding-right: 40px;
    padding-left: 40px;
    padding-bottom: 0.25em;
    font-weight: bold;
    font-size: 0.8em;
}

/*Paragraph Styling*/

.art230 p {
    margin-bottom: 0.65em;
    line-height: 1.5em;
    font-size: 1.15em;
}

.art230 p.intro {
    margin-bottom: 0.75em;
    margin-top: 0em;
    color: black;
    font-style: normal;
    line-height: 1.5em;
    font-size: 1.5em;
    margin-left: 0px;
}

.art230 p.caption {
    margin-top: 10px;
    margin-bottom: 20px;
    border-bottom-color: #cfcecc;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    padding-bottom: 20px;
    padding-top: 0px;
    line-height: 1.25em;
    font-size: 0.65em;
    text-transform: none;
}

.art230 p.time {
    font-style: normal;
    margin-top: .5em;
    padding-bottom: .5em;
    font-size: 1em;
    color: #9aa44c;
    border-bottom: #dedbd1;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    background-size: 1em;
    background-repeat: no-repeat;
    background-position: 1px 3px;
    background-image: url(time.png);
    padding-left: 1.5em;
}

.art230 p.assessment {
    font-style: normal;
    margin-top: -0.35em;
    padding-bottom: .35em;
    font-size: .8em;
    color: #a2987b;
    border-bottom: #dedbd1;
    border-bottom-style: dotted;
    border-bottom-width: 1px;
    background-size: 1em;
    background-repeat: no-repeat;
    background-position: 1px 3px;
}

/*Link Styling*/

.art230 a:link {
    color: #cb5417;
    text-decoration: none;
    padding-left: 0px;
    padding-right: 0px;
    border-bottom-style: dotted;
    border-bottom-width: 1px;
}

/*List Styling*/

.art230 ul {
    margin-left: 1em;
    margin-bottom: 1.25em;
    margin-right: 1em;
    margin-top: .5em;
}

.art230 li {
    list-style-position: outside;
    margin-top: 0px;
    margin-left: .5em;
    margin-bottom: .25em;
    list-style-type: square;
    line-height: 1.5em;
    font-size: 0.9em;
}

.art230 li.Checklists {

    list-style-position: outside;
    margin-top: 2px;
    margin-left: .5em;
    margin-bottom: .5em;
    list-style-type: square;
    line-height: 1.5em;
    font-size: 1em;
}

/*Other Styling*/

.art230 blockquote {
    margin-bottom: 1.5em;
    margin-top: 1.5em;
    margin-left: 0px;
    color: #cb5417;
    line-height: 1.5em;
    font-size: 2em;
}

.art230 img {
    margin-top: 10px;
    margin-bottom: 10px;
    border-width: 1px;
    border-style: solid;
    border-color: #cfcecc;
    float: left;
    margin-right: 1em;
    max-width: 100%;
    clear: both;
}

.art230 .attrib {
    margin-bottom: 0.75em;
    margin-top: 0em;
    color: black;
    font-style: normal;
    line-height: 1.5em;
    margin-left: 0px;
    font-size: 1em;
    color: #877058;
}

.art230 .important {
    margin-bottom: 1.em;
    margin-top: 1.em;
    margin-left: 0em;
    color: #cb5417;
    line-height: 1.em;
    font-size: 1em;
}

/*End of Art Styling*/

